import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
var QQMapWX = require('../../utils/qqmap-wx-jssdk.min.js');
var qqmapsdk;
export default {
	data() {
		return {
			data: {
				ykj:0,
				bj:0
			},
			latitude: "",
			longitude: "",
			dangqian: "",
			master_info:"",
			auto:0
		}
	},
	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady() {
		this.mapCtx = wx.createMapContext('myMap')
	},
	onLoad() {
		this.loadMap();
	},
	onShow() {
		//获取用户服务地址
		var _this=this;
		setTimeout(function(){
			if (app.globalData.isRequest == 1) {
				_this.master_info=app.globalData.master_info;
				_this.loadData();
			} else {
				// 由于 getUserInfo 是网络请求，可能会在 Page.onLoad 之后才返回
				// 所以此处加入 callback 以防止这种情况
				app.isRequestCallback = isRequest => {
					if (isRequest == 1) {
						_this.master_info=app.globalData.master_info;
						_this.loadData();
					}
				}
			}
		},1000);
	},
	methods: {
		//移到自己定位坐标
		moveToLocation() {
			this.mapCtx.moveToLocation()
		},
		loadMap() {
			qqmapsdk = new QQMapWX({
				key: 'WCYBZ-EWVC4-CC3U2-DZGZI-UWGK2-E2BMM'
			});
			wx.getLocation({
				type: 'gcj02',
				success: res => {
					qqmapsdk.reverseGeocoder({
						location: {
							latitude: res.latitude,
							longitude: res.longitude
						},
						success: addressRes => {
							this.latitude=res.latitude;
							this.longitude= res.longitude;
							this.dangqian=addressRes.result.address_component.city;
						}
					})
				},
				fail: err => {
					console.log(err);
					if (err.errMsg === "getLocation:fail auth deny") {
						wx.showModal({
							title: '提示',
							showCancel: false,
							confirmText: '好的',
							content: '未获取到你的地理位置，暂时无法为你提供服务。请检查是否开启定位权限。或尝试重新打开小程序。',
							success: (res) => {
								if (res.confirm) {
									wx.openSetting({
										success(settingdata) {
											console.log(settingdata)
											if (settingdata.authSetting['scope.userLocation']) {
												wx.showToast({
													title: '授权成功',
													icon: 'none',
													mask: true,
												})
											} else {
												wx.showToast({
													title: '授权失败',
													icon: 'none',
													mask: true,
												})
											}
										}
									})
								}
							}
						})
					}
				}
			});
		},
		//如果没有授权登陆，则接单范围里显示所有服务项目，不显示门店订单，一口价订单和报价订单显示当前定位的师傅位置
		loadData() {
			if(this.master_info&&this.master_info.status == 5){
				var id=this.master_info.id;
				this.auto=this.master_info.auto;
				this.loadMasterInfo(id);
			}else{
				var id=0;
				this.loadMasterInfo(id);
			}
		},
		loadMasterInfo(id){
			this.request({
				url: interfaces.initIndex,
				data: {
					id: id,
				},
				method: 'POST',
				success: res => {
					if(res.code==200){
						this.data=res.data;
					}
				}
			})
		},
		//开启或关闭自动接单
		autoOrder(auto){
			var _this=this;
			if(auto==1){
				var content="确定要关闭自动接单吗？"
			}else{
				var content="确定要开启自动接单吗？"
			}
			if(this.master_info){
				wx.showModal({
					title: '提示',
					content: content,
					success: function(res) {
						if (res.confirm) {
							_this.request({
								url: interfaces.autoOrder,
								header: {
									token: _this.master_info.token
								},
								data: {
									auto: auto
								},
								method: 'POST',
								success: res => {
									console.log(res);
									if (res.code == 200) {
										wx.showToast({
											title: "操作成功",
											icon: 'success'
										})
										_this.master_info.auto=res.data;
										_this.auto=res.data;
									} else {
										app.isLogin(res.code, res.msg);
									}
								}
							})
						}
					}
				})
			}else{
				wx.reLaunch({
					url: '/pages/auth/index',
				})
			}
		},
		start(id,index){
			var _this=this;
			wx.showModal({
				title: '提示',
				content: '确定要接单吗？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderLsStart,
							header: {
								token: _this.master_info.token
							},
							data: {
								id: id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									wx.showToast({
										title: res.msg,
										icon: 'success'
									})
									_this.data.store.splice(index,1);
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},
		//跳转接单大厅
		goHall(type){
			wx.navigateTo({
				url: "../order/hall?type=" + type,
			})
		},
		//跳转连锁店订单详情页
		goLsDetail(id) {
			wx.navigateTo({
				url: '/pages/order/lsdetail?id=' + id,
			})
		},
	}
}
